module.exports = " /* Dropdown Button */\n .dropbtn {\n    width: 130px;\n    height: 60px;\n    background-color: #4CAF50;\n    color: white;\n    padding: 16px;\n    font-size: 16px;\n    border: none;\n  }\n .searchbtn {\n    width: 205px;\n    height: 60px;\n    background-color: #4CAF50;\n    color: white;\n    padding: 16px;\n    font-size: 16px;\n    border: none;\n  }\n /* The container <div> - needed to position the dropdown content */\n .dropdown {\n    position: relative;\n    display: inline-block;\n  }\n .scan-dropdown {\n    display: none;\n    position: absolute;\n    background-color: #f1f1f1;\n    min-width: 130px;\n    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);\n    z-index: 1;\n  }\n .scan-dropdown a {\n    color: black;\n    padding: 12px 16px;\n    text-decoration: none;\n    display: block;\n  }\n /* Dropdown Content (Hidden by Default) */\n .dropdown-content {\n    display: none;\n    position: absolute;\n    background-color: #f1f1f1;\n    min-width: 160px;\n    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);\n    z-index: 1;\n  }\n /* Links inside the dropdown */\n .dropdown-content a {\n    color: black;\n    padding: 12px 16px;\n    text-decoration: none;\n    display: block;\n  }\n img.animated-gif{\n    width: 130px;\n    height: 60px;\n    background-color: #4CAF50;\n    color: white;\n    padding: 16px;\n    font-size: 16px;\n    border: none;\n  }\n /* Change color of dropdown links on hover */\n .dropdown-content a:hover {background-color: #ddd;}\n .scan-dropdown a:hover {background-color: #ddd;}\n /* Show the dropdown menu on hover */\n .dropdown:hover .dropdown-content {display: block;}\n .dropdown:hover .scan-dropdown {display: block;}\n /* Change the background color of the dropdown button when the dropdown content is shown */\n .dropdown:hover .dropbtn {background-color: #3e8e41;}\n .dropdown:hover .searchbtn {background-color: #3e8e41;} \n/*# sourceMappingURL=data:application/json;base64, */"